@font-face {
  font-family: 'Gotham Pro';
  src: url("../font/GothamPro/GothamPro.eot");
  src: url("../font/GothamPro/GothamPro.eot?#iefix") format("embedded-opentype"), url("../font/GothamPro/GothamPro.woff") format("woff"), url("../font/GothamPro/GothamPro.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Gotham Pro';
  src: url("../font/GothamPro/GothamPro-Medium.eot");
  src: url("../font/GothamPro/GothamPro-Medium.eot?#iefix") format("embedded-opentype"), url("../font/GothamPro/GothamPro-Medium.woff") format("woff"), url("../font/GothamPro/GothamPro-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: 'Gotham Pro';
  src: url("../font/GothamPro/GothamPro-Bold.eot");
  src: url("../font/GothamPro/GothamPro-Bold.eot?#iefix") format("embedded-opentype"), url("../font/GothamPro/GothamPro-Bold.woff") format("woff"), url("../font/GothamPro/GothamPro-Bold.ttf") format("truetype");
  font-weight: 600;
  font-style: normal;
}

body {
  font-family: 'Gotham Pro', sans-serif;
  font-weight: 400;
  background: #fff;
  font-size: 16px;
  color: #000;
  line-height: 1.38;
  -ms-overflow-style: none;
}

body::-webkit-scrollbar {
  display: none;
}

.wrapper-content {
  max-width: 1108px;
  margin: 0 auto;
  padding: 0 30px;
}

.row.non-offset {
  margin: 0;
}

.offset-p-none {
  padding: 0;
}

.pp-scrollable {
  -ms-overflow-style: none;
}

.pp-scrollable::-webkit-scrollbar {
  display: none;
}

.pattern {
  background: url(../images/patern.svg) no-repeat calc(100% - 63px) #fff;
}

.subtitle {
  color: #000;
  font-size: 40px;
  font-weight: 500;
  line-height: 1.13;
  max-width: 200px;
}

input:focus,
input:active,
textarea:focus,
textarea:active,
button:focus,
button:active,
span:focus,
span:active,
button:focus,
button:active {
  outline: none;
}

select:focus,
select:hover,
option:hover,
option:focus {
  outline: none;
}

.vh-100 {
  height: 100vh;
  min-height: 525px;
}

.t-green {
  color: #16ca56;
}

.title {
  font-size: 40px;
  line-height: 1.13;
  font-weight: 500;
  max-width: 590px;
}

.t-grey {
  color: #636363;
}

.t-medium {
  font-weight: 500;
}

.fz-18 {
  font-size: 18px;
}

.wrap-input {
  width: 100%;
}

.top-s {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  border-bottom: 1px solid #f0f0f0;
}

.top-s .subtitle {
  margin: 50px 0;
}

.input-styles {
  font-family: 'Gotham Pro', sans-serif;
  width: 100%;
  background: transparent;
  padding-bottom: 17px;
  font-weight: 500;
  color: #000;
  font-size: 16px;
  border: none;
  border-bottom: 3px solid #c7c7c7;
}

.input-styles::-webkit-input-placeholder {
  color: #a3a3a3;
}

.input-styles::-moz-placeholder {
  color: #a3a3a3;
}

.input-styles:-moz-placeholder {
  color: #a3a3a3;
}

.input-styles:-ms-input-placeholder {
  color: #a3a3a3;
}

.btn-styles {
  display: inline-block;
  padding: 26px;
  background: #16ca56;
  text-decoration: none;
  text-align: center;
  color: #fff;
  font-weight: 500;
  font-size: 16px;
  box-shadow: 11px 11px 21px 0 rgba(22, 202, 86, 0.18);
  transition: all .3s ease-in-out;
}

.btn-styles:hover {
  filter: brightness(1.1);
}

.pp-tooltip {
  color: #fff;
  text-transform: uppercase;
  font-size: 16px;
  background: #16ca56;
  padding: 12px;
  border-radius: 4px;
}

.pp-tooltip:after {
  content: '';
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 7px 0 7px 8px;
  border-color: transparent transparent transparent #16ca56;
  position: absolute;
  top: 4px;
  right: -7px;
}

#pp-nav ul li a.active span {
  background: none;
}

#pp-nav ul li a.active span:before {
  content: '';
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #16ca56;
  position: absolute;
  left: calc((100% - 8px) / 2);
  top: calc((100% - 8px) / 2);
}

#pp-nav ul li a span {
  width: 16px;
  height: 16px;
  border-color: #16ca56 !important;
  border: 1px solid #16ca56;
}

.menu {
  background: #f0f0f0;
  height: 100vh;
  min-height: 525px;
}

.menu .fixed {
  padding: 45px 100px 0 45px;
  height: 100vh;
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.menu .fixed:after {
  content: '';
  width: .5px;
  top: 0;
  height: 100%;
  background: #c8c8c8;
  position: absolute;
  right: 70.5px;
}

.menu .logo img {
  height: 53px;
}

.menu nav .navigation {
  display: flex;
  flex-direction: column;
}

.menu nav .navigation li {
  margin-top: 21px;
  position: relative;
  transition: all .3s;
}

.menu nav .navigation li:hover:not(.active) {
  text-shadow: 2px 2px 0 #c8c8c8;
}

.menu nav .navigation li:before {
  content: '';
  width: 9px;
  height: 9px;
  background: #a3a3a3;
  position: absolute;
  right: -34.5px;
  z-index: 3;
  top: calc((100% - 9px) / 2);
}

.menu nav .navigation li.active:before {
  background: #1fd860;
}

.menu nav .navigation li.active a {
  color: #000;
}

.menu nav .navigation li a {
  color: #a3a3a3;
  text-decoration: none;
}

.menu .tel {
  font-size: 18px;
  color: #000;
}

.menu .social-network {
  margin: 38px -30px 0 -45px;
  display: flex;
  justify-content: space-between;
  border-top: 1px solid #c8c8c8;
}

.menu .social-network .network {
  padding: 10px 0;
  flex-basis: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.menu .social-network .network:nth-child(odd) {
  border-right: 1px solid #c8c8c8;
}

.menu .social-network a:hover img {
  transform: translateY(-3px);
}

.menu .social-network a.telegram img {
  height: 53px;
}

.menu .social-network a img {
  height: 29px;
  transition: all .3s ease-in-out;
}

.home {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-top: 166px;
  margin-bottom: 190px;
}

.home .content {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 50px;
}

.home .left {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.home .time {
  text-align: center;
  font-size: 80px;
  font-weight: 600;
}

.home .form {
  margin-top: 30px;
  display: flex;
  justify-content: space-between;
}

.home .form .wrap-input {
  margin-top: 15px;
  margin-right: 30px;
}

.home .block-img img {
  width: 100%;
  height: auto;
  object-fit: cover;
  min-height: 372px;
}

.works .info {
  padding: 44px 0;
  background: #fff;
}

.works .steps {
  display: flex;
  margin-top: 80px;
}

.works .steps .enumeration:first-child .task {
  padding-left: 0;
  margin-left: 60px;
}

.works .steps .step p {
  max-width: 130px;
  width: 100%;
  font-size: 92px;
  color: #16ca56;
  font-weight: 500;
  position: relative;
}

.works .steps .step p:before {
  content: '';
  width: 21px;
  height: 21px;
  border-radius: 50%;
  background: #fff;
  border: 1px dashed #979797;
  position: absolute;
  bottom: -19px;
  left: calc((100% - 21px) / 2);
}

.works .steps .task {
  padding-left: 60px;
  border-top: 1px dashed #979797;
  margin-top: 9px;
  padding-top: 24px;
}

.works .steps .task p {
  font-size: 18px;
  font-weight: 500;
  max-width: 175px;
}

.works .steps .task p:first-child {
  padding-left: 0;
}

.services {
  padding: 50px 0;
}

.services .top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.services .tabs__caption {
  display: flex;
}

.services .tabs__caption li {
  color: #a3a3a3;
  font-size: 20px;
  font-weight: 500;
  transition: all .3s ease-in-out;
  margin-right: 72px;
  padding: 22px 0;
  border-bottom: 3px solid transparent;
  cursor: pointer;
}

.services .tabs__caption li.active {
  color: #000;
  border-bottom: 3px solid #1fd860;
}

.services .download {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid #f0f0f0;
  cursor: pointer;
  transition: all .3s ease-in-out;
}

.services .download:hover {
  background: #16ca56;
}

.services .download:hover p {
  color: #fff;
}

.services .download:hover a img {
  filter: brightness(10);
}

.services .download p {
  color: #000;
  font-size: 20px;
  font-weight: 500;
  padding-left: 22px;
  transition: all .3s ease-in-out;
}

.services .download a {
  padding: 42px;
  border-left: 1px solid #f0f0f0;
}

.services .card {
  padding: 22px;
  border: 1px solid #f0f0f0;
  transition: all .3s ease-in-out;
  cursor: pointer;
}

.services .card:hover {
  color: #fff;
  background: #16ca56;
  border: 1px solid #16ca56;
}

.services .card:hover .price {
  color: #fff;
}

.services .card:hover .style-plus {
  border: 1px solid #fff;
}

.services .card:hover .style-plus:before, .services .card:hover .style-plus:after {
  background: #fff;
}

.services .card .top-card {
  display: flex;
  justify-content: space-between;
}

.services .card .style-plus {
  width: 34px;
  height: 34px;
  background: transparent;
  border: 1px solid #16ca56;
  position: relative;
  display: inline-block;
}

.services .card .style-plus:before {
  content: '';
  width: 14.5px;
  height: 1px;
  position: absolute;
  background: #16ca56;
  top: calc((100% - 1px) / 2);
  left: calc((100% - 14.5px) /2);
}

.services .card .style-plus:after {
  content: '';
  width: 1px;
  height: 14.5px;
  position: absolute;
  background: #16ca56;
  top: calc((100% - 14.5px) /2);
  left: calc((100% - 1px) / 2);
}

.services .card .price {
  color: #000;
  font-size: 20px;
  font-weight: 500;
  padding-top: 12px;
  transition: all .3s ease-in-out;
}

.services .card .price.rub:after {
  content: '\20BD';
}

.services .tabs__content {
  display: none;
  background: #fff;
}

.services .tabs__content.active {
  display: block;
}

.guarantees .list-guarantees {
  margin-top: 10px;
}

.guarantees .list-guarantees .list-item {
  display: flex;
  margin-top: 10%;
}

.guarantees .list-guarantees .list-item .block-img {
  max-width: 95px;
  width: 100%;
  position: relative;
}

.guarantees .list-guarantees .list-item .block-img:before {
  content: '';
  opacity: .3;
  background-image: linear-gradient(to bottom, #16ca56, rgba(22, 202, 86, 0));
  width: 56px;
  height: 45px;
  position: absolute;
  bottom: -9px;
  right: 0;
}

.guarantees .list-guarantees .list-item .block-img.circle:before {
  border-radius: 50%;
  bottom: 5px;
}

.guarantees .list-guarantees .list-item p {
  padding-left: 40px;
  color: #000;
  font-size: 18px;
  font-weight: 500;
  max-width: 312px;
}
